@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: none;
  clip: rect(0 0 0 0);
}

html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-style: normal;
  font-weight: 400;
  font-family: "Open Sans", "Arial", sans-serif;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  display: block;
  height: auto;
}

.main-header {
  position: relative;
  border-bottom: 1px solid #ececec;
}

.main-header__wrapper {
  min-width: 320px;
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 30px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 22px;
  padding-bottom: 22px;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .no-js .main-header__wrapper {
    display: flex;
    flex-direction: column;
    padding: 0;
  }
}

@media (max-width: 767px) {
  .no-js .main-header__logo {
    padding: 21px 30px;
  }
}
.main-header__logo:hover {
  opacity: 0.6;
}
.main-header__logo:focus-visible {
  outline: 2px solid #000000;
  outline-offset: 2px;
}
.main-header__logo:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-header__logo {
    margin: 20px 0;
  }
}
@media (min-width: 1150px) {
  .main-header__logo {
    position: absolute;
    z-index: 2;
  }
}

.main-header__toggle {
  position: absolute;
  top: 32px;
  right: 31px;
  display: block;
  width: 20px;
  height: 14px;
  padding: 0;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  cursor: pointer;
}
.main-header__toggle::before, .main-header__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #231f20;
  transition: all 0.3s ease 0s;
}
.main-header__toggle::before {
  top: 0;
}
.main-header__toggle::after {
  bottom: 0;
}
.no-js .main-header__toggle {
  display: none;
}
.main-header__toggle:hover {
  opacity: 0.6;
}
.main-header__toggle:focus-visible {
  outline: 1px solid #000000;
  outline-offset: 2px;
}
.main-header__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}

.main-header__toggle-icon {
  position: absolute;
  top: 6px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #231f20;
  transition: all 0.3s ease 0s;
}

.is-open .main-header__toggle::before {
  top: 6px;
  color: #000000;
  transform: rotate(45deg);
}

.is-open .main-header__toggle::after {
  bottom: 6px;
  color: #000000;
  transform: rotate(-45deg);
}

.is-open .main-header__toggle-icon {
  height: 0;
}

