  border: 2px solid #444444;
  resize: none;
}
.textarea__input::placeholder {
  font-size: 17px;
  line-height: 18px;
  font-family: "Open Sans", "Arial", sans-serif;
  opacity: 0.3;
}
@media (min-width: 768px) {
  .textarea__input::placeholder {
    font-size: 19px;
  }
}
.textarea__input:hover, .textarea__input:active {
  border-color: #62d1ba;
}
.textarea__input:focus-visible {
  border-color: #62d1ba;
  outline: none;
}
@media (min-width: 1150px) {
  .textarea__input {
    width: 700px;
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
}
.modal.is-open {
  display: flex;
}

.modal__container {
  position: relative;
  width: 320px;
  margin: auto;
  padding: 29px 30px 46px 30px;
  background-color: #ffffff;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px 0 rgba(0, 1, 1, 0.25);
}
@media (min-width: 768px) {
  .modal__container {
    width: 566px;
    padding: 38px 105px 46px 105px;
  }
}

.modal__form {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.modal__form-title {
  margin: 0 0 14px 0;
  font-size: 25px;
  line-height: 30px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .modal__form-title {
    margin-bottom: 11px;
    font-size: 30px;
    line-height: 57px;
  }
}

.modal__form-text {
  margin: 0 0 22px 0;
  font-size: 15px;
  line-height: 30px;
  color: #444444;
}
@media (min-width: 768px) {
  .modal__form-text {
    margin-bottom: 20px;
    font-size: 17px;
  }
}

.modal__form-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 31px;
}
@media (min-width: 768px) {
  .modal__form-list {
    width: 350px;
  }
}

.modal__form-label {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  color: #444444;
  text-transform: uppercase;
}

.modal__form-input {
  width: 82px;
  margin: 0;
  padding: 24px 22px;
  border: 2px solid rgba(68, 68, 68, 0.1);
  cursor: pointer;
  appearance: none;
}
.modal__form-input:checked {
  border-color: #63d1bb;
}
.modal__form-input:hover {
  border-color: rgba(68, 68, 68, 0.3);
}
@media (min-width: 768px) {
  .modal__form-input {
    width: 112px;
  }
}

.modal__form-size {
  position: absolute;
}

.modal__form-button {
  padding: 18px 67px;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #63d1bb;
  border: none;
  cursor: pointer;
}
.modal__form-button:hover {
  opacity: 0.6;
}
.modal__form-button:focus-visible {
  outline: 2px solid #000000;
}
.modal__form-button:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .modal__form-button {
    width: 350px;
  }
}